import React from "react"
import { FaArrowAltCircleRight, FaUniversity } from "react-icons/fa"
import Background from "../images/backgroundblog.jpg"
import Layout from "../components/layout"
import { Link, graphql } from "gatsby"

import "../styles/index.css"
import Head from "../components/Head"

export const query = graphql`
  query {
    allContentfulUnits {
      edges {
        node {
          title
          description
          slug
          image {
            title
            file {
              url
            }
          }
        }
      }
    }
  }
`

const Services = props => {
  const data = props.data.allContentfulUnits

  return (
    <Layout>
      <Head title="Services" />
      <section
        className="page-title"
        style={{ backgroundImage: `url(${Background})` }}
      >
        <div className="auto-container">
          <div className="text-center">
            <h1>Units</h1>
          </div>
        </div>
      </section>

      {/* { <!--Practic Areas-->} */}
      <section className="Practice-space">
        <div className="container">
          <div className="row">
            {data.edges.map(edge => {
              return (
                <div className="col-sm-6 col-md-4" key={edge.node.title}>
                  <div className="practise-area">
                    <div className="thumb">
                      <img
                        src={edge.node.image.file.url}
                        alt={edge.node.image.title}
                      />
                    </div>
                    <div className="practise-details">
                      <div className="round-style"></div>
                      <i className="icon">
                        <FaUniversity />
                      </i>
                      <h4 className="title">{edge.node.title}</h4>
                      <p className="details">{edge.node.description}</p>
                      <Link
                        to={edge.node.slug}
                        style={{
                          boxShadow: `none`,
                          textDecoration: `none`,
                          color: `inherit`,
                        }}
                        className="btn-thm"
                      >
                        Read More
                        <i className="FaArrowAltCircleRight">
                          <FaArrowAltCircleRight />
                        </i>
                      </Link>
                    </div>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </section>
    </Layout>
  )
}
export default Services
